.responder-look {
  background: #2a273b;
  width: 100%;
  height: calc(100% - 100px);
  padding: 19px;
  display: flex;
  overflow: hidden;
}
.responder-look .responder-look-left {
  position: relative;
  height: calc(100vh - 138px);
  padding-left: 54px;
  flex: 1;
}
.responder-look .responder-look-left .ant-col {
  width: 150px;
  height: 180px;
  background: #302e45;
  box-shadow: 0px 2px 10px 0px #232031;
}
.responder-look .responder-look-left > p.activity_title {
  font-size: 18px;
  color: #fff;
  line-height: 16px;
  padding-left: 10px;
  margin-bottom: 20px;
  margin-left: 16px;
  height: 25px;
  position: relative;
  line-height: 25px;
}
.responder-look .responder-look-left > p.activity_title::before {
  content: '';
  height: 20px;
  width: 3px;
  border-radius: 1.5px;
  position: absolute;
  background: #ffffff;
  left: 0;
  top: 50%;
  transform: translate(0px, -50%);
}
.responder-look .responder-look-left .responder-list {
  height: calc(100% - 109px);
  min-width: 771px;
  overflow: auto;
}
.responder-look .responder-look-left .responder-list::-webkit-scrollbar {
  display: none;
}
.responder-look .responder-look-left .responder-list .ant-col {
  padding: 20px 10px 0;
}
.responder-look .responder-look-left .responder-list .ant-col div {
  text-align: center;
}
.responder-look .responder-look-left .responder-list .ant-col div h4 {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
}
.responder-look .responder-look-left .responder-list .ant-col div img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  margin-bottom: 9px;
}
.responder-look .responder-look-left .responder-list .ant-col h3 {
  font-size: 16px;
  color: #fff;
  text-align: center;
  padding-top: 11px;
  margin-top: 22px;
  border-top: 1px solid rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
.responder-look .responder-look-right {
  color: rgba(255, 255, 255, 0.8);
  height: 100%;
}
.responder-look .responder-look-right .ant-row {
  margin: 18px 0;
}
.responder-look .responder-look-right .ant-row img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.responder-look .responder-look-right > div {
  background: #302e45;
  width: 280px;
  padding: 0 20px;
}
.responder-look .responder-look-right > div h3 {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.responder-look .responder-look-right > div h3 span {
  margin: 0 5px;
}
.responder-look .responder-look-right > div:nth-of-type(1) {
  height: 36%;
  min-height: 36%;
  margin-bottom: 20px;
  text-align: center;
}
.responder-look .responder-look-right > div:nth-of-type(1) img {
  width: 70px;
  height: 70px;
  margin: 0px 0 10px 0;
}
.responder-look .responder-look-right > div:nth-of-type(1) span {
  color: #F7B500;
}
.responder-look .responder-look-right > div:nth-of-type(2) {
  height: 61%;
}
.responder-look .responder-look-right > div:nth-of-type(2) > div {
  height: calc(100% - 53px);
  position: relative;
}
.responder-look .responder-look-right > div:nth-of-type(2) p {
  text-align: center;
}
.responder-look .responder-look-right .ant-progress-inner {
  width: 150px !important;
  height: 150px !important;
  margin: 100px 55px 0;
}
.responder-look .responder-look-right .ant-progress-text {
  color: #fff;
}
.input-rating div {
  background: none;
}
.input-rating .ant-modal-content {
  background: url("../../../../assets/images/interact/vote-bg.png") no-repeat;
  background-size: 100%;
}
.empty-lh {
  color: #fff;
  position: absolute;
  top: calc(50% - 110px);
  left: calc(50% - 122px);
}
.empty-lh .ant-empty-image {
  margin-bottom: 20px;
  width: 230px;
  height: 150px;
}
.empty-lh .ant-empty-image img {
  width: 230px;
  height: 150px;
}
.screen-left .responder-look-right > div .anticon-history svg {
  color: white;
}
.screen-left .public .ant-col {
  margin: 0px  16px;
  margin-bottom: 35px;
}
.responder-list .ant-empty .ant-empty-image {
  width: 233px;
  height: 150px;
  margin-bottom: 24px;
}
.responder-list .ant-empty .ant-empty-image img {
  width: 100%;
  height: 100%;
}
.responder-list .ant-empty p {
  font-size: 14px;
}
.activity_title {
  font-size: 18px !important;
  color: #fff;
  padding-left: 10px;
  margin-bottom: 20px;
  margin-left: 16px;
  height: 25px !important;
  position: relative;
  line-height: 25px !important;
  border: none !important;
}
.activity_title::before {
  content: '';
  height: 20px;
  width: 3px;
  border-radius: 1.5px;
  position: absolute;
  background: #ffffff;
  left: 0;
  top: 50%;
  transform: translate(0px, -50%);
}
.scrollBar {
  overflow: auto;
}
.scrollBar::-webkit-scrollbar {
  display: none;
}
